@charset "UTF-8";

/*  */

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  height: 0;
  display: block;
  clear: both;
  visibility: hidden;
  overflow: hidden;
}

.clearfix {
  *zoom: 1;
  /*IE/7/6*/
}

/**/

.fl {
  float: left;
}

.fr {
  float: right;
}

.hide {
  display: none;
}

.wrap {
  width: 7.5rem;
  margin: 0 auto;
}

/* start*/

.flex-father {
  display: flex;
  display: -webkit-box;
  display: -moz-box;
  display: -o-box;
  display: -ms-box;
}

.flex-father .flex-child1 {
  -webkit-box-flex: 1;
}

/* end*/

/* start*/

.translateCenter {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

/* end*/

.pt10 {
  padding-top: 0.1rem;
}

.pt23 {
  padding-top: 0.23rem;
}

.ft40 {
  font-size: 0.4rem;
}


